import { Edit, MoreVertical, Search } from "react-feather";
import { FormattedMessage } from "react-intl";
import { Link } from "react-router-dom";
import {
  Badge,
  DropdownItem,
  DropdownMenu,
  DropdownToggle,
  UncontrolledDropdown,
} from "reactstrap";

const statusObj = {
  1: {
    class: "light-success",
    text: <FormattedMessage id="Active" />,
  },
  // 0: {
  //   class: "light-warning",
  //   text: <FormattedMessage id="Deactive" />,
  // },
  0: {
    class: "light-secondary",

    text: <FormattedMessage id="Blocked" />,
  },
};

const certificateObj = {
  1: {
    class: "light-primary",
    text: <FormattedMessage id="Advantage" />,
  },
  0: {
    class: "light-warning",
    text: <FormattedMessage id="Professional" />,
  },
  2: {
    class: "light-secondary",

    text: <FormattedMessage id="Basic" />,
  },
  3: {
    class: "light-danger",

    text: <FormattedMessage id="Pratice" />,
  },
};

export const columns = [
  {
    name: <FormattedMessage id="id" />,
    maxWidth: "80px",
    selector: "id",
    cell: (row) => row.id,
  },
  {
    name: <FormattedMessage id="Branch Code" />,
    minWidth: "120px",
    selector: "code_branch",
    cell: (row) => row.code_branch,
  },
  {
    name: <FormattedMessage id="Branch name" />,
    minWidth: "120px",
    selector: "name",
    cell: (row) => row.name,
  },
  // {
  //   name: <FormattedMessage id="Description" />,
  //   minWidth: "120px",
  //   selector: "content",
  //   cell: (row) => {
  //     let maxLength = 45;
  //     let contentString = row.content;
  //     if (contentString.length > maxLength) {
  //       contentString = contentString.slice(0, maxLength) + "...";
  //     }
  //     return (
  //       <span
  //         dangerouslySetInnerHTML={{
  //           __html: contentString,
  //         }}
  //       ></span>
  //     );
  //   },
  // },
  {
    name: <FormattedMessage id="address" />,
    minWidth: "120px",
    selector: "addpress",
    cell: (row) => row.addpress,
  },
  {
    name: <FormattedMessage id="email" />,
    minWidth: "120px",
    selector: "email",
    cell: (row) => row.email,
  },
  {
    name: <FormattedMessage id="phoneNumber" />,
    minWidth: "120px",
    selector: "phone",
    cell: (row) => row.phone,
  },
  {
    name: <FormattedMessage id="Staff" />,
    minWidth: "120px",
    selector: "staffId",
    cell: (row) => {
      if (row?.staff?.first_name && row?.staff?.last_name) {
        return row?.staff?.first_name + " " + row?.staff?.last_name;
      } else {
        return "";
      }
    },
  },
  {
    name: <FormattedMessage id="status" />,
    minWidth: "170px",
    selector: "status",
    cell: (row) => (
      <Badge className="text-capitalize" color={statusObj[row.status]?.class}>
        {statusObj[row.status]?.text}
      </Badge>
    ),
  },
  {
    name: <FormattedMessage id="action" />,
    minWidth: "100px",

    cell: (row) => {
      return (
        row?.id && (
          <div className="mh-100 d-flex align-items-center justify-content-center mw-100">
            <Badge
              className=" w-100"
              pill
              color="primary"
              style={{ padding: "10px 25px" }}
            >
              <Link
                className="text-edit hover:text-white"
                to={`/apps/cms/branch/edit/${row.id}`}
              >
                {" "}
                <FormattedMessage id="edit" />
              </Link>
            </Badge>
          </div>
        )
      );
    },
  },
];
