import Sidebar from "@components/sidebar";
import { isObjEmpty } from "@utils";
import classnames from "classnames";
import { useEffect, useState } from "react";
import { Controller, useForm } from "react-hook-form";
import { FormattedMessage, injectIntl } from "react-intl";
import "react-phone-number-input/style.css";
import { useDispatch, useSelector } from "react-redux";
import { Button, Form, FormGroup, Input, Label, Media, Row } from "reactstrap";
import validateOptions from "@constants/validate";
import { addBranch, getStaff } from "../store/action";
import { Editor } from "react-draft-wysiwyg";
import { convertToRaw, EditorState } from "draft-js";
import { toast } from "react-toastify";
import draftToHtml from "draftjs-to-html";
import Select from "react-select";
import { validateEditor } from "../../../../../helper/common";

const SidebarNewBranch = ({
  open,
  toggleSidebar,
  intl,
  disable,
  setDisable,
}) => {
  const store = useSelector((state) => state.branch);
  const { data } = useSelector((state) => state.staffs);
  const [isSubmit, setIsSubmit] = useState(false);
  const lang = useSelector((state) => state.common.language);
  const BranchOptions = validateOptions.BranchOptions;
  const dispatch = useDispatch();
  const { register, errors, control, setError, setValue, handleSubmit } =
    useForm();
  const [changeSelect, setChangeSelect] = useState();
  const [description, setDescription] = useState(" ");
  const [valueContent, setValueContent] = useState(EditorState.createEmpty());

  useEffect(() => {
    if (store?.err?.statusCode) {
      setDisable(false);
    }
  }, [store?.err]);

  const handleOnchangeStateEditor = (data) => {
    setValueContent(data);

    const description =
      data && draftToHtml(convertToRaw(data.getCurrentContent()));
    setDescription(description);
  };

  useEffect(() => {
    dispatch(
      getStaff({
        filter: {
          // phone: searchPhone || undefined,
          // status: filterStatus?.value || undefined,
          // last_name: searchLastName || undefined,
          // first_name: searchFirstName || undefined,
          // roleId: filterRole?.value || undefined,
          // email: searchEmail || undefined,
          status: 1,
        },
        // skip: 0,
        // limit: 20,
        order: {
          key: "createdAt",
          value: "desc",
        },
      })
    );
  }, []);

  const onSubmit = async (values) => {
    if (isObjEmpty(errors)) {
      const description =
        (valueContent &&
          draftToHtml(convertToRaw(valueContent?.getCurrentContent()))) ||
        null;
      setDisable(true);

      setDisable(true);
      dispatch(
        addBranch({
          ...values,
          name: values.name || undefined,
          code_branch: values.code_branch || "",
          addpress: values.addpress || undefined,
          email: values.email || undefined,
          phone: values.phone || undefined,
          status: parseInt(values.status),
          description: description || " ",
          lang,
        })
      );
    }
  };

  return (
    <Sidebar
      size="lg"
      open={open}
      title={<FormattedMessage id="Add New Branch" />}
      headerClassName="mb-1"
      contentClassName="pt-0"
      toggleSidebar={toggleSidebar}
    >
      <Form onSubmit={handleSubmit(onSubmit)}>
        <FormGroup>
          <Label for="name">
            <FormattedMessage id="Branch name" />{" "}
            <span className="text-danger">*</span>
          </Label>
          <Input
            name="name"
            id="name"
            placeholder=""
            innerRef={register(BranchOptions.name)}
            onBlur={() => {
              let name = document.getElementById("name");
              if (name && name.value) {
                name.value = name.value.trim();
              }
            }}
            className={classnames({ "is-invalid": errors["name"] })}
          />
          <small className="text-danger">
            {errors?.name && errors.name.message}
          </small>
          {errors?.name?.type == "validate" && (
            <small className="text-danger">
              <FormattedMessage id="Invalid name" />
            </small>
          )}
        </FormGroup>
        <FormGroup>
          <Label for="code_branch">
            <FormattedMessage id="Branch Code" />{" "}
            <span className="text-danger">*</span>
          </Label>
          <Input
            name="code_branch"
            id="code_branch"
            placeholder=""
            innerRef={register(BranchOptions.code_branch)}
            onBlur={() => {
              let code_branch = document.getElementById("code_branch");
              if (code_branch && code_branch.value) {
                code_branch.value = code_branch.value.trim();
              }
            }}
            className={classnames({ "is-invalid": errors["code_branch"] })}
          />
          <small className="text-danger">
            {errors?.code_branch && errors.code_branch.message}
          </small>
          {errors?.code_branch?.type == "validate" && (
            <small className="text-danger">
              <FormattedMessage id="Invalid name" />
            </small>
          )}
        </FormGroup>

        <FormGroup>
          <Label for="description">
            <FormattedMessage id="Description" />{" "}
          </Label>
          <Editor
            stripPastedStyles={true}
            toolbar={{
              options: ["inline", "textAlign"],
              inline: {
                inDropdown: false,
                options: ["bold", "italic", "underline"],
              },
            }}
            editorState={valueContent}
            onEditorStateChange={(data) => setValueContent(data)}
            name="description"
            innerRef={register}
          />
        </FormGroup>

        <FormGroup>
          <Label for="addpress">
            <FormattedMessage id="address" />{" "}
            <span className="text-danger">*</span>
          </Label>
          <Input
            name="addpress"
            id="addpress"
            placeholder=""
            innerRef={register(BranchOptions.addpress)}
            onBlur={() => {
              let addpress = document.getElementById("addpress");
              if (addpress && addpress.value) {
                addpress.value = addpress.value.trim();
              }
            }}
            className={classnames({ "is-invalid": errors["addpress"] })}
          />
          <small className="text-danger">
            {errors?.addpress && errors.addpress.message}
          </small>
          {errors?.addpress?.type == "validate" && (
            <small className="text-danger">
              <FormattedMessage id="Invalid name" />
            </small>
          )}
        </FormGroup>
        <FormGroup>
          <Label for="email">
            <FormattedMessage id="Email" />{" "}
            <span className="text-danger">*</span>
          </Label>
          <Input
            name="email"
            id="email"
            placeholder=""
            innerRef={register(BranchOptions.email)}
            onBlur={() => {
              let email = document.getElementById("email");
              if (email && email.value) {
                email.value = email.value.trim();
              }
            }}
            className={classnames({ "is-invalid": errors["email"] })}
          />
          <small className="text-danger">
            {errors?.email && errors.email.message}
          </small>
          {errors?.email?.type == "validate" && (
            <small className="text-danger">
              <FormattedMessage id="Invalid name" />
            </small>
          )}
        </FormGroup>
        <FormGroup>
          <Label for="phone">
            <FormattedMessage id="phoneNumber" />{" "}
            <span className="text-danger">*</span>
          </Label>
          <Input
            name="phone"
            id="phone"
            placeholder=""
            innerRef={register(BranchOptions.phone)}
            onBlur={() => {
              let phone = document.getElementById("phone");
              if (phone && phone.value) {
                phone.value = phone.value.trim();
              }
            }}
            className={classnames({ "is-invalid": errors["phone"] })}
          />
          <small className="text-danger">
            {errors?.phone && errors.phone.message}
          </small>
          {errors?.phone?.type == "validate" && (
            <small className="text-danger">
              <FormattedMessage id="Invalid name" />
            </small>
          )}
        </FormGroup>

        <FormGroup>
          <Label>
            <FormattedMessage id="Staff" />
            <span className="text-danger"> * </span>
          </Label>
          <Controller
            control={control}
            name="staffId"
            render={({ field }) => {
              return (
                <Select
                  id="staffId"
                  innerRef={register}
                  name="staffId"
                  className={classnames(
                    "react-select",
                    !changeSelect
                      ? {
                          "is-invalid": errors["staffId"],
                        }
                      : ""
                  )}
                  options={data?.map((item, index) => {
                    return {
                      value: item?.id,
                      label: item?.username,
                      number: index + 1,
                    };
                  })}
                  classNamePrefix="select"
                  {...field}
                  onChange={(e) => {
                    setError("staffId", "");
                    setValue("staffId", e?.value);
                    setChangeSelect(e?.value);
                  }}
                />
              );
            }}
          ></Controller>
          <small className="text-danger">
            {errors?.staffId && errors.staffId.message}
          </small>
        </FormGroup>

        <FormGroup>
          <Label for="status">
            <FormattedMessage id="status" />{" "}
          </Label>
          <Input
            type="select"
            name="status"
            id="status"
            innerRef={register({ required: true })}
          >
            <option value="1">{intl.formatMessage({ id: "Active" })}</option>

            <option value="0">{intl.formatMessage({ id: "Blocked" })}</option>
          </Input>
        </FormGroup>

        <div style={{ textAlign: "end" }}>
          <Button
            type="submit"
            className="mr-1"
            color="primary"
            disabled={disable}
          >
            <FormattedMessage id="Save" />
          </Button>
          <Button
            type="reset"
            color="secondary"
            outline
            onClick={toggleSidebar}
          >
            <FormattedMessage id="Cancel" />
          </Button>
        </div>
      </Form>
    </Sidebar>
  );
};

export default injectIntl(SidebarNewBranch);
